





import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({
    name: 'PreviewImage',
})
export default class PreviewImage extends Vue {
    /**  */
    @Prop({ type: String }) public src?: string;

    public onClick() {
        if (this.src) {
            // 
            wx.previewImage({
                // http
                current: this.src,
                // http
                urls: [ this.src ],
            });
        }
    }
}
