<template>
    <div ref="container" class="list-footer" @click="onClick">
        <span>{{hint}}</span>
    </div>
</template>

<script>
/**
 * @click 
 */
export default {
    name: 'ListFooter',
    props: {
        /**
         * 
         */
        count: {
            type: Number,
        },
        /**
         * 
         */
        allLoaded: {
            required: true,
            type: Boolean,
        },
        /**
         * 
         */
        requesting: {
            type: Boolean,
            default: false,
        },
        /**
         * scroll
         */
        detect: {
            type: Boolean,
            default: true,
        },
        /**
         * 
         */
        loadedText: {
            type: String,
        },
    },
    data() {
        return {
            /**
             * scroll
             */
            scroll: true,
        };
    },
    computed: {
        hint() {
            if (this.allLoaded) {
                return this.count === 0 ? '' : (this.loadedText || '~');
            } else {
                if (this.requesting) {
                    return '';
                } else {
                    if (this.scroll) {
                        return '';
                    } else {
                        return '';
                    }
                }
            }
        },
    },
    watch: {
        detect(val) {
            this.scroll = val;
        },
    },
    methods: {
        onClick() {
            if (!this.allLoaded) {
                this.$emit('click');
            }
        },
        scrollCallback() {
            if (!this.scroll) {
                // 
                return;
            }
            // 
            const $container = this.$refs.container;
            if (!$container) {
                return;
            }

            const rect = $container.getBoundingClientRect();
            // 
            if (rect.top < window.innerHeight * 1.3 && rect.bottom > 0) {
                this.$emit('click');
            }
            return true;
        },
    },
    /**
     * 
     */
    mounted() {
        this.scroll = this.detect;
        // 
        if (this.detect) {
            this.$store.commit('addScrollCallback', this.scrollCallback.bind(this));
        }
    },
    beforeDestroy() {
        // callback
        this.scroll = false;
    },
};
</script>

<style lang="less">
    @import "../../lib/style/util.less";
    @import "../../lib/style/mixins.less";
    .list-footer {
        text-align: center;
        .px2rem6(padding, 15, 0);
        .px2rem6(font-size, 14);
        color: @second-font-color;
    }
</style>