import Vue from 'vue';
import { Module } from 'vuex';
import { RootState } from '@/types/store';
import { isUndef } from '@/lib/utils';

export interface DistrictState {
    _provinceList: District[];
    /**  */
    extraProvinceList: District[];
    _listMap: {
        [districtId: number]: District[],
    };
    _districtMap: {
        [districtId: number]: District,
    };
}

/**
 * id-10
 *
 *  {id, name}
 */
const m: Module<DistrictState, RootState> = {
    state: {
        // province
        _provinceList: [],
        extraProvinceList: [
            {
                id: -1,
                name: '',
                level: 1,
            },
        ],
        //  id -> list
        _listMap: {},
        // districtmapid -> district<{id, level, name}>
        _districtMap: {
            '-1': {
                id: -1,
                name: '',
                level: 1,
            },
        },
    },
    mutations: {
        /**
         * data
         *
         * @param {Number} id id
         * @param {Array} list list
         */
        addList(state, { id, list }) {
            Vue.set(state._listMap, id, list);
            // map
            list.forEach((district: District) => {
                Vue.set(state._districtMap, String(district.id), district);
            });
        },
        /**
         * provinceList
         *
         * @param {Array} provinceList province [{id: 1, name: ''}]
         */
        setProvinceList(state, provinceList) {
            state._provinceList = provinceList;
        },
    },
    actions: {
        /**
         * 
         *
         * @param {} forceUpdate 
         *
         * @return {Array} ProvinceList
         */
        async getProvinceList({ state, commit }, { forceUpdate } = {}) {
            if (state._provinceList.length === 0) {
                const response = await Vue.axios.get('/api/v1/framework/top_level_districts');
                const rdata = response.data;
                if (rdata.status === 0) {
                    const data = rdata.data as District[];
                    commit('setProvinceList', data);
                    commit('addList', {
                        id: 0, // provinceListid0
                        list: data,
                    });
                }
            }
            return state._provinceList;
        },

        /**
         * 
         *
         * @param {} forceUpdate 
         * @param {} id id
         *
         * @return {} 
         */
        async getChildDistricts({ state, commit }, { forceUpdate, id } = {}) {
            if (isUndef(state._listMap[id]) || forceUpdate) {
                const response = await Vue.axios.get('/api/v1/framework/child_districts', {
                    params: {
                        id,
                    },
                });
                const rdata = response.data;
                if (rdata.status === 0) {
                    commit('addList', {
                        id,
                        list: rdata.data,
                    });
                }
            }
            return state._listMap[id];
        },
    },
};

export default m;
