









import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({
    name: 'PreviewImageList',
})
export default class PreviewImage extends Vue {
    /**  */
    @Prop({ type: Array, default() { return []; } }) public src?: string[];

    public onClick(image: string) {
        if (this.src) {
            // 
            wx.previewImage({
                // http
                current: image,
                // http
                urls: this.src,
            });
        }
    }
}
