


























import { Component, Vue, Watch, Prop } from 'vue-property-decorator';
import store from '@/store/index';
import {
    isUndef,
} from '@/lib/utils';

/**
 * v-model
 *
 * @input(index) indexnavigations
 */
@Component({
    name: 'SlideNav',
})
export default class SlideNav extends Vue {
    /** valuetext */
    @Prop({ required: true, type: Array, default() { return []; } }) public navigations!: ValueText[];
    /**  */
    @Prop({ type: [ String, Number ] }) public value!: string | number;
    /**  */
    @Prop({ type: [ Object, Array ] }) public containerClass!: object;
    /**  */
    @Prop({ type: [ Object, Array ] }) public wrapClass!: object;
    /** item */
    @Prop({ type: [ Object, Array ] }) public itemClass!: object;
    /** indicator */
    @Prop({ type: [ Object, Array ] }) public indicatorClass!: object;
    /**  left/center/right */
    @Prop({ type: String, default: 'center' }) public contentPosition!: string;
    /** scrollIntoView */
    @Prop({ type: Boolean, default: false }) public isScrollIntoView!: boolean;
    /** indicator */
    @Prop({ type: Boolean, default: true }) public indicatorVisible!: boolean;

    /**
     * valuescrollIntoViewtabparent
     */
    @Watch('value')
    public onChangeValue(val: string | number) {
        if (this.isScrollIntoView) {
            const el = this.$refs[val] as HTMLElement | undefined;
            if (el && el.scrollIntoView) {
                el.scrollIntoView();
            }
        }
    }

    @Watch('isScrollIntoView')
    public onIsScrollIntoViewChanged(val: boolean) {
        if (val) {
            const el = this.$refs[this.value] as HTMLElement | undefined;
            if (el && el.scrollIntoView) {
                el.scrollIntoView();
            }
        }
    }

    /**
     * 
     */
    public get percentage() {
        if (isUndef(this.navigations) || this.navigations.length === 0) {
            return 0;
        } else {
            return ((1 / this.navigations.length) * 100).toFixed(2);
        }
    }
    /**
     * index
     */
    public get selectedIndex() {
        let result = 0;
        this.navigations.forEach((navigation, index) => {
            if (navigation.value === this.value) {
                result = index;
            }
        });
        return result;
    }

    /**
     * 
     */
    public onClick(value: string | number) {
        this.$emit('input', value);
    }
    public mounted() {
        if (this.isScrollIntoView) {
            const el = this.$refs[this.value] as HTMLElement | undefined;
            if (el && el.scrollIntoView) {
                el.scrollIntoView();
            }
        }
    }
}
