










import { Component, Vue, Prop } from 'vue-property-decorator';
import { VNode } from 'vue';

/**
 * 
 *
 * 
 *  - containerPageCount 3 3
 *  - contentPageCount 2 2
 *  - containerPageIndex 1 1
 *  - contentPageOffset 1 1
 */
@Component({
    name: 'HorizontalSlideContainer',
})
export default class HorizontalSlideContainer extends Vue {
    /**  */
    @Prop({ required: true, type: Number }) public current!: number;
    /**  */
    @Prop({ required: true, type: Number }) public containerPageCount!: number;
    /**  */
    @Prop({ required: true, type: Number }) public contentPageCount!: number;
    /** 110 */
    @Prop({ required: true, type: Number, default: 0 }) public containerPageIndex!: number;
    /** container0 */
    @Prop({ required: true, type: Number, default: 0 }) public contentPageOffset!: number;

    /**
     * %
     */
    public get containerPageX() {
        return -(this.containerPageIndex / this.containerPageCount * 100).toFixed(4) + '%';
    }
    /**
     * %
     */
    public get contentPageLeft() {
        return (this.contentPageOffset / this.containerPageCount * 100).toFixed(4) + '%';
    }
    /**
     * %
     */
    public get contentPageWidth() {
        return (this.contentPageCount / this.containerPageCount * 100).toFixed(4) + '%';
    }
    /**
     * 
     */
    public get slidePageWidth() {
        return (1 / this.contentPageCount * 100).toFixed(4) + '%';
    }

    // @ts-ignore
    public render(h) {
        const vm = this;
        // @ts-ignore
        const pages = this.$slots.default.filter((vnode) => {
            //  && vnode.tag.indexOf('SwiperSlide') != -1
            return vnode != null && vnode.tag != null;
        });
        // slidePage
        pages.forEach((page: VNode) => {
            if (!page.data) {
                page.data = {
                    style: {
                        width: vm.slidePageWidth,
                    },
                };
            }
            if (!page.data.style) {
                page.data.style = {
                    width: vm.slidePageWidth,
                };
                return;
            }
            // @ts-ignore
            page.data.style.width = vm.slidePageWidth;
        });

        const slideContent = h('div', {
            class: [ 'horizontal-slide-content', 'clearfix' ],
            style: {
                left: this.contentPageLeft,
                width: this.contentPageWidth,
                transform: `translate3d(-${(this.current * 1 / this.contentPageCount * 100).toFixed(4) + '%'}, 0, 0)`,
            },
        }, pages);

        const slideContainer = h('div', {
            class: [ 'horizontal-slide-container' ],
            style: {
                width: this.containerPageCount + '00%',
                transform: 'translate3d(' + this.containerPageX + ', 0, 0)',
            },
        }, [ slideContent ]);

        const slideWrap = h('div', {
            class: [ 'horizontal-slide-wrap' ],
        }, [ slideContainer ]);

        return slideWrap;
    }
}
